import React from "react";

function Theme({ setToSettings, theme }) {
  const change = (e) => {
    setToSettings("theme", e.target.value);
  };

  return (
    <div className="appl1_sgpt_app_settings_theme_section">
      <div
        className="appl1_sgpt_section_header"
        style={{
          marginBottom: "20px",
        }}
      >
        Theme
      </div>
      <div
        className="appl1_sgpt_app_settings_theme_section_container"
        onChange={change}
      >
        <label
          className={
            theme === "auto"
              ? "appl1_sgpt_app_settings_theme_section_item active"
              : "appl1_sgpt_app_settings_theme_section_item"
          }
        >
          <input
            type="radio"
            value="auto"
            name="theme"
            className="appl1_sgpt_app_settings_theme_section_item_input"
          ></input>
          <div
            className={
              theme === "auto"
                ? "appl1_sgpt_app_settings_theme_section_item_icon circle active"
                : "appl1_sgpt_app_settings_theme_section_item_icon circle"
            }
          ></div>
          <p
            className={
              theme === "auto"
                ? "appl1_sgpt_app_settings_theme_section_item_text active"
                : "appl1_sgpt_app_settings_theme_section_item_text"
            }
          >
            Auto
          </p>
        </label>

        <label
          className={
            theme === "light"
              ? "appl1_sgpt_app_settings_theme_section_item active"
              : "appl1_sgpt_app_settings_theme_section_item"
          }
        >
          <input
            type="radio"
            value="light"
            name="theme"
            className="appl1_sgpt_app_settings_theme_section_item_input"
          ></input>
          <div
            className={
              theme === "light"
                ? "appl1_sgpt_app_settings_theme_section_item_icon sun active"
                : "appl1_sgpt_app_settings_theme_section_item_icon sun"
            }
          ></div>
          <p
            className={
              theme === "light"
                ? "appl1_sgpt_app_settings_theme_section_item_text active"
                : "appl1_sgpt_app_settings_theme_section_item_text"
            }
          >
            Light
          </p>
        </label>

        <label
          className={
            theme === "dark"
              ? "appl1_sgpt_app_settings_theme_section_item active"
              : "appl1_sgpt_app_settings_theme_section_item"
          }
        >
          <input
            type="radio"
            value="dark"
            name="theme"
            className="appl1_sgpt_app_settings_theme_section_item_input"
          ></input>
          <div
            className={
              theme === "dark"
                ? "appl1_sgpt_app_settings_theme_section_item_icon moon active"
                : "appl1_sgpt_app_settings_theme_section_item_icon moon"
            }
          ></div>
          <p
            className={
              theme === "dark"
                ? "appl1_sgpt_app_settings_theme_section_item_text active"
                : "appl1_sgpt_app_settings_theme_section_item_text"
            }
          >
            Dark
          </p>
        </label>
      </div>
    </div>
  );
}

export default Theme;
