import React, { useState } from "react";
import Markdown from "./Markdown";

function DialogElementHeader({ item, index }) {
  const [copyStatus, setCopyStatus] = useState(false);
  const copyAnswer = () => {
    setCopyStatus(true);

    navigator.clipboard.writeText(item.a);
    setTimeout(() => {
      setCopyStatus(false);
    }, 2500);
  };

  return (
    <div className="appl1_sgpt_cnt_dialog_answer_header" key={index}>
      <div className="appl1_sgpt_cnt_dialog_answer_header_icon_box">
        <div className="appl1_sgpt_cnt_dialog_answer_header_icon"></div>
        <div>ChatGPT responded:</div>
      </div>

      <div
        className="appl1_sgpt_cnt_dialog_answer_header_box"
        onClick={copyAnswer}
      >
        <div
          className="appl1_sgpt_cnt_dialog_answer_header_box_copy"
          style={{
            display: copyStatus === false ? "block" : "none",
          }}
        ></div>
        <div
          className="appl1_sgpt_cnt_dialog_answer_header_box_check"
          style={{
            display: copyStatus === true ? "block" : "none",
          }}
        ></div>
      </div>
    </div>
  );
}

export default function SGPTDialog({
  answerStack,
  stopRespBtnStatus,
  stopResponse,
}) {
  if (document.querySelector(".appl1_sgpt_cnt_dialog") !== null) {
    document.querySelector(".appl1_sgpt_cnt_dialog").scrollTop =
      document.querySelector(".appl1_sgpt_cnt_dialog").scrollHeight;
  }

  return (
    <div className="appl1_sgpt_cnt_dialog">
      {answerStack.map((el, index) => {
        return (
          <div key={index}>
            <p className="appl1_sgpt_cnt_dialog_question">{el.q}</p>
            <div className="appl1_sgpt_cnt_dialog_answer">
              <DialogElementHeader item={el} index={index} />
              <Markdown answer={el.a} />
            </div>
          </div>
        );
      })}

      <div
        style={{
          display: stopRespBtnStatus === false ? "none" : "flex",
        }}
        className="appl1_sgpt_cnt_dialog_stop_resp"
        onClick={stopResponse}
      >
        <span className="appl1_sgpt_cnt_dialog_stop_resp_icon"></span>
        <p className="appl1_sgpt_cnt_dialog_stop_resp_text">Stop response</p>
      </div>
    </div>
  );
}
