import React from "react";

export default function Helper({ setToSettings, status }) {
  const change = (e) => {
    setToSettings("siteHelper", e.target.checked);
  };

  return (
    <div className="appl1_sgpt_app_settings_helper_section">
      <label className="appl1_sgpt_app_settings_helper_section_switch">
        <input
          type="checkbox"
          checked={status}
          onChange={change}
          className="appl1_sgpt_app_settings_helper_section_switch_input"
        ></input>
        <span className="appl1_sgpt_app_settings_helper_section_slider_round"></span>
      </label>
      <p className="appl1_sgpt_app_settings_helper_section_text">
        Add chatGPT helper to all sites
      </p>
    </div>
  );
}
