import React from "react";

export default function Dailogs({ setToSettings, status }) {
  const change = (e) => {
    setToSettings("removeDi", e.target.checked);
  };

  return (
    <div className="appl1_sgpt_app_settings_remove_section">
      <label className="appl1_sgpt_app_settings_remove_section_switch">
        <input
          type="checkbox"
          checked={status}
          onChange={change}
          className="appl1_sgpt_app_settings_remove_section_switch_input"
        ></input>
        <span className="appl1_sgpt_app_settings_remove_section_slider_round"></span>
      </label>
      <p className="appl1_sgpt_app_settings_remove_section_text">
        Remove dialogs generated by search
      </p>
    </div>
  );
}
