import React from "react";

function Language({ setToSettings, language }) {
  const defLayng = language.filter((el) => el.selected === true);
  const change = (e) => {
    const list = language.map((el) => {
      if (el.value === e.target.value) {
        el.selected = true;
      } else {
        el.selected = false;
      }

      return el;
    });

    setToSettings("language", list);
  };

  return (
    <div className="appl1_sgpt_app_settings_language_section">
      <div
        className="appl1_sgpt_section_header"
        style={{
          marginBottom: "18px",
        }}
      >
        Language
      </div>
      <div className="appl1_sgpt_app_settings_language_section_container">
        <select
          className="appl1_sgpt_app_settings_language_section_container_select"
          name="language"
          id="language"
          onChange={change}
          defaultValue={defLayng[0].value}
        >
          {language.map((i, index) => {
            return (
              <option
                key={index}
                className="appl1_sgpt_app_settings_language_section_container_select_option"
                value={i.value}
                // selected={i.selected}
              >
                {i.name}
              </option>
            );
          })}
        </select>
      </div>
    </div>
  );
}

export default Language;
