import React, { Component } from "react";

export default class Provider extends Component {
  state = {
    keyValue: "",
    provider: null,
    btnActive: false,
    showWarning: false,
    flag: false,
  };

  componentDidMount() {
    this.setState({
      provider: this.props.provider,
      keyValue: this.props.provider.apiKey,
    });
  }

  clickOnTab = (e) => {
    const { provider } = this.state;
    const { value } = e.target.attributes["data-tab"];

    if (provider.currentTab !== value) {
      provider.currentTab = value;
      this.setState({
        provider,
      });
    }
  };

  selectedModels = (e) => {
    const { provider } = this.state;
    const list = provider.models.map((el) => {
      if (el.value === e.target.value) {
        el.selected = true;
      } else {
        el.selected = false;
      }

      return el;
    });

    provider.models = list;
    this.setState({ provider });
  };

  changeInput = (e) => {
    this.setState({
      keyValue: e.target.value,
    });
  };

  guideTrigger = (e) => {
    const { provider } = this.state;

    provider.guideMode = e.target.value;
    this.setState({
      provider,
    });
  };

  onClickSave = () => {
    const { keyValue, provider } = this.state;

    if (keyValue === "" && provider.currentTab === "gpt3") {
      this.showWarning();
    } else {
      provider.apiKey = keyValue;
      this.setState(
        {
          provider,
        },
        () => {
          this.props.setToSettings("provider", this.state.provider);
        }
      );
    }
  };

  showWarning = () => {
    this.setState({ showWarning: true }, () => {
      setTimeout(() => {
        this.setState({
          showWarning: false,
        });
      }, 3000);
    });
  };

  render() {
    const { provider, keyValue, showWarning, flag } = this.state;
    let defModels;

    if (provider) {
      if (provider.currentTab === "gpt3" && flag === false) {
        setTimeout(() => {
          document.querySelector(".appl1_sgpt_app_settings_wrapper").scrollTop =
            document.querySelector(
              ".appl1_sgpt_app_settings_wrapper"
            ).scrollHeight;
        }, 200);
        this.setState({
          flag: true,
        });
      }
      defModels = provider.models.filter((el) => el.selected === true);
    }

    return (
      <div className="appl1_sgpt_app_settings_provider_section">
        <div
          className="appl1_sgpt_section_header"
          style={{
            marginBottom: "16px",
          }}
        >
          AI Provider
        </div>
        {provider !== null ? (
          <div className="appl1_sgpt_app_settings_provider_section_container">
            <div className="appl1_sgpt_app_settings_provider_section_container_head">
              <p
                style={{
                  marginRight: "21px",
                }}
                data-tab="web"
                onClick={this.clickOnTab}
                className={
                  provider.currentTab === "web"
                    ? "appl1_sgpt_app_settings_provider_section_container_head_text active"
                    : "appl1_sgpt_app_settings_provider_section_container_head_text"
                }
              >
                ChatGPT Webapp
              </p>
              <p
                data-tab="gpt3"
                onClick={this.clickOnTab}
                className={
                  provider.currentTab === "gpt3"
                    ? "appl1_sgpt_app_settings_provider_section_container_head_text active"
                    : "appl1_sgpt_app_settings_provider_section_container_head_text"
                }
              >
                OpenAI API
              </p>
            </div>
            <div className="appl1_sgpt_app_settings_provider_section_container_content">
              {provider.currentTab === "web" ? (
                <p
                  className="appl1_sgpt_app_settings_provider_section_container_content_p"
                  style={{
                    marginBottom: "18px",
                  }}
                >
                  Use the same API as the ChatGPT webapp.<br></br>Require login
                  to ChatGPT webapp.<br></br>GPT-4 model will be used for
                  ChatGPT Plus users.<br></br>GPT-3.5 model will be used for
                  free users.
                </p>
              ) : (
                <div className="appl1_sgpt_app_settings_provider_container">
                  <p
                    className="appl1_sgpt_app_settings_provider_section_container_content_p"
                    style={{
                      marginBottom: "18px",
                    }}
                  >
                    OpenAI official API (GPT-3), more stable,
                    <br></br>
                    <span
                      style={{
                        fontWeight: "600",
                      }}
                    >
                      charge by usage
                    </span>
                  </p>

                  <select
                    className="appl1_sgpt_app_settings_provider_container_select"
                    name="models"
                    id="models"
                    onChange={this.selectedModels}
                    defaultValue={defModels[0].value}
                  >
                    {provider.models.map((i, index) => {
                      return (
                        <option
                          key={index}
                          className="appl1_sgpt_app_settings_provider_container_select_options"
                          value={i.value}
                          // selected={i.selected}
                        >
                          {i.name}
                        </option>
                      );
                    })}
                  </select>

                  <label className="appl1_sgpt_app_settings_provider_container_label">
                    <div className="appl1_sgpt_app_settings_provider_container_div">
                      API key
                    </div>
                    <input
                      className="appl1_sgpt_app_settings_provider_container_input"
                      onChange={this.changeInput}
                      value={keyValue}
                      type="password"
                    ></input>
                  </label>

                  <p
                    style={{
                      marginBottom: "19px",
                      fontSize: "13px",
                    }}
                  >
                    You can find or create your API key{" "}
                    <a
                      href="https://platform.openai.com/account/api-keys"
                      target="_blank"
                      rel="noreferrer"
                      style={{ color: "#592cff" }}
                    >
                      here
                    </a>
                  </p>

                  <p
                    style={{
                      fontSize: "13px",
                      fontWeight: "600",
                      marginBottom: "7px",
                    }}
                  >
                    GPT-3 Prompt Guide
                  </p>
                  <p
                    style={{
                      // fontStyle: "inherit",
                      lineHeight: "1.5",
                      marginBottom: "13px",
                      fontSize: "12px",
                    }}
                  >
                    "Prompt guide" is a piece of text that is automatically
                    added before the user's message to teach GPT-3 to act as a
                    QA chatbot like ChatGPT.
                  </p>

                  <div
                    className="appl1_sgpt_app_settings_provider_guidemode"
                    onChange={this.guideTrigger}
                  >
                    <label className="appl1_sgpt_app_settings_provider_guidemode_label">
                      <input
                        type="radio"
                        value="default"
                        name="guide"
                        className="appl1_sgpt_app_settings_provider_guidemode_label_input"
                      />
                      <div
                        style={{
                          display: "flex",
                          alignItems: "flex-start",
                          marginBottom: "12px",
                        }}
                      >
                        <div
                          className={
                            provider.guideMode === "default"
                              ? "appl1_sgpt_trigger_container_item_circle active"
                              : "appl1_sgpt_trigger_container_item_circle"
                          }
                          style={{
                            marginRight: "15px",
                          }}
                        >
                          <div
                            className={
                              provider.guideMode === "default"
                                ? "appl1_sgpt_trigger_container_item_circle_inner active"
                                : "appl1_sgpt_trigger_container_item_circle_inner"
                            }
                          ></div>
                        </div>
                        <div
                          className="appl1_sgpt_app_settings_provider_guidemode_text"
                          style={{
                            marginBottom: "10px",
                          }}
                        >
                          <p
                            className="appl1_sgpt_app_settings_provider_guidemode_text_p"
                            style={{
                              fontWeight: "700",
                              fontSize: "15px",
                              marginBottom: "5px",
                            }}
                          >
                            Default prompt guide
                          </p>
                          <p
                            className="appl1_sgpt_app_settings_provider_guidemode_text_p2"
                            style={{
                              lineHeight: "1.2",
                            }}
                          >
                            Generates better response, costs more tokens (220
                            tokens for guide)
                          </p>
                        </div>
                      </div>
                    </label>
                    <label className="appl1_sgpt_app_settings_provider_guidemode_label">
                      <input
                        type="radio"
                        value="simple"
                        name="guide"
                        className="appl1_sgpt_app_settings_provider_guidemode_label_input"
                      />
                      <div
                        style={{
                          display: "flex",
                          alignItems: "flex-start",
                        }}
                      >
                        <div
                          className={
                            provider.guideMode === "simple"
                              ? "appl1_sgpt_trigger_container_item_circle active"
                              : "appl1_sgpt_trigger_container_item_circle"
                          }
                          style={{
                            marginRight: "15px",
                          }}
                        >
                          <div
                            className={
                              provider.guideMode === "simple"
                                ? "appl1_sgpt_trigger_container_item_circle_inner active"
                                : "appl1_sgpt_trigger_container_item_circle_inner"
                            }
                          ></div>
                        </div>
                        <div className="appl1_sgpt_app_settings_provider_guidemode_text">
                          <p
                            style={{
                              fontWeight: "600",
                              fontSize: "16px",
                              marginBottom: "10px",
                              fontStyle: "normal",
                            }}
                            className="appl1_sgpt_app_settings_provider_guidemode_text_p"
                          >
                            Simple prompt guide
                          </p>
                          <p
                            className="appl1_sgpt_app_settings_provider_guidemode_text_p2"
                            style={{
                              lineHeight: "1.2",
                            }}
                          >
                            Generates better response, costs more tokens (35
                            tokens for guide)
                          </p>
                        </div>
                      </div>
                    </label>
                  </div>
                </div>
              )}
            </div>
            <div
              className="appl1_sgpt_app_settings_provider_section_container_btn"
              onClick={this.onClickSave}
            >
              Save
            </div>
            <p
              className="appl1_sgpt_app_settings_provider_section_container_warningtext"
              style={{
                display: showWarning === false ? "none" : "block",
              }}
            >
              Please, enter API key
            </p>
          </div>
        ) : null}
      </div>
    );
  }
}
