import React, { Component } from "react";
import {
  detectSystemColorScheme,
  endsWithQuestionMark,
  getBrowser,
} from "../../services/utils";

import SGPTMain from "./SGPTMain";
import Settings from "../../popup/Settings/Settings";

export default class SGPTContainer extends Component {
  state = {
    showLoginForm: false,
    headerText: "",
    loader: true,
    settings: null,
    showSettings: false,
    showContent: false,
    browser: null,
  };

  componentDidMount() {
    const _this = this;

    chrome.storage.local.get(["appl1_sgpt_settings"], (result) => {
      this.setState({
        settings: result.appl1_sgpt_settings,
        browser: getBrowser(),
      });

      chrome.runtime.sendMessage({ action: "GET_ACCESS_TOKEN" }, (data) => {
        if (data.authStatus === false) {
          _this.setState({
            showLoginForm: true,
            headerText: "Action required",
            loader: false,
          });
        } else {
          _this.setState({
            showLoginForm: false,
            headerText: `ChatGPT for ${
              getBrowser() === "CHROME" ? "Chrome" : "Edge"
            }`,
            loader: false,
          });
        }
      });
    });
  }

  getColorTheme = () => {
    if (this.state.settings !== null) {
      let userTheme;
      const { theme } = this.state.settings;

      if (theme === "auto") {
        userTheme = detectSystemColorScheme();
      } else {
        userTheme = theme;
      }

      return userTheme;
    }
  };

  openSettings = () => {
    this.setState({
      showSettings: !this.state.showSettings,
    });
  };

  changesInSettings = (type, value) => {
    const { settings } = this.state;
    settings[type] = value;

    this.setState(
      {
        settings,
      },
      () => this.satSettingsToStore()
    );
  };

  satSettingsToStore = () => {
    chrome.runtime.sendMessage({
      action: "SET_SETTINGS_TO_STORE",
      data: this.state.settings,
    });
  };

  getExtVersion = () => {
    return chrome.runtime.getManifest().version;
  };

  clickOnShowContent = () => {
    this.setState({
      showContent: true,
    });
  };

  render() {
    const {
      showLoginForm,
      headerText,
      loader,
      showSettings,
      settings,
      showContent,
      browser,
    } = this.state;

    let viewInContent;

    if (settings !== null) {
      const mainContent = (
        <>
          {this.props.question !== "" ? (
            <SGPTMain
              headerText={headerText}
              showLoginForm={showLoginForm}
              showSettings={showSettings}
              openSettings={this.openSettings}
              question={this.props.question}
              getExtVersion={this.getExtVersion}
              clickOnShowContent={this.clickOnShowContent}
              settings={settings}
              imHelper={this.props.imHelper}
              browser={browser}
            />
          ) : (
            <div
              className="appl1_sgpt_cnt_box_text"
              style={{
                animation: "none",
                color: "#592cff",
              }}
            >
              Please enter a request
            </div>
          )}

          {showSettings === false ? null : (
            <Settings
              settings={settings}
              setToSettings={this.changesInSettings}
              extVersion={this.getExtVersion}
              browser={browser}
            />
          )}
        </>
      );
      const { triggerMode } = settings;

      if (showSettings === false) {
        if (triggerMode === "question") {
          if (endsWithQuestionMark(this.props.question) === true) {
            viewInContent = mainContent;
          } else {
            viewInContent = (
              <div className="appl1_sgpt_cnt_main_triggetText_question">
                Trigger ChatGPT by appending a question mark after your query
              </div>
            );
          }
        }

        if (triggerMode === "manually") {
          if (showContent === true) {
            viewInContent = mainContent;
          } else {
            viewInContent = (
              <div
                onClick={this.clickOnShowContent}
                className="appl1_sgpt_cnt_main_triggetText_manually"
              >
                Ask ChatGPT for this query
              </div>
            );
          }
        }

        if (triggerMode === "always") {
          viewInContent = mainContent;
        }
      } else {
        viewInContent = mainContent;
      }
    }

    return (
      <>
        {settings !== null ? (
          <div
            className={
              this.getColorTheme(settings.theme) === "dark"
                ? "appl1_sgpt_cnt appl1_sgpt_cnt_dark"
                : "appl1_sgpt_cnt"
            }
            style={{
              display: this.props.boxMode === "aForm" ? "block" : "none",
            }}
          >
            <div className="appl1_sgpt_cnt_box">
              {loader === false ? (
                viewInContent
              ) : (
                <p className="appl1_sgpt_cnt_box_text">Please waite...</p>
              )}
            </div>
          </div>
        ) : null}
      </>
    );
  }
}
