import React, { Component } from "react";
import Markdown from "./Markdown";
import SGPTDialog from "./SGPTDialog";
import SGPTMainFooterText from "./SGPTMainFooterText";
import SGPTMainFooter from "./SGPTMainFooter";

let myPort;

