export default class SGPTMain extends Component {
  state = {
    answer: null,
    answerStack: [
      {
        q: this.props.question,
        a: "",
      },
    ],
    nextQuestion: false,
    dialogLoader: false,
    dialog: false,
    showFooter: false,
    loader: true,
    textareaText: "",
    stopRespBtnStatus: false,
    showFeedbackText: false,
    feedbackTextColor: "",
    showFeedbackBtn: false,
    errorText: null,
    showCopyBox: false,
    copyStatus: false,
  };

  componentDidMount() {
    this.postQuestion(this.props.question, (event) => {
      this.createAnswer(event);
    });
  }

  componentDidUpdate(prevProps) {
    if (this.props.question !== prevProps.question) {
      myPort.disconnect();

      this.setState({
        showFooter: false,
        showFeedbackBtn: false,
        showCopyBox: false,
        answer: null,
        loader: true,
        answerStack: [
          {
            q: this.props.question,
            a: "",
          },
        ],
        dialog: false,
      });

      this.postQuestion(this.props.question, (event) => {
        this.createAnswer(event);
      });
    }
  }

  postQuestion = (question, cb) => {
    myPort = chrome.runtime.connect();

    if (question !== "") {
      myPort.postMessage({ question: question });
    }
    myPort.onMessage.addListener((event) => {
      cb(event);
    });
  };

  createAnswer = (event) => {
    if (event.type === "error") {
      if (event.text.code) {
        return;
      } else {
        this.setState({
          errorText: event.text,
          loader: false,
          dialogLoader: false,
        });
      }

      return;
    }

    let type = event.type;
    const arr = this.state.answerStack;
    const { nextQuestion } = this.state;

    if (nextQuestion === true) {
      arr.push({
        q: this.state.textareaText,
        a: "",
      });

      this.setState({
        nextQuestion: false,
      });
    }

    if (type === "answer") {
      arr[arr.length - 1].a = event.data.text;

      this.setState({
        answer: event,
        answerStack: arr,
        loader: false,
        dialogLoader: false,
        textareaText: "",
        stopRespBtnStatus: true,
      });
    } else if (type === "done") {
      this.setState({
        showFooter: true,
        showFeedbackBtn: true,
        showCopyBox: true,
        stopRespBtnStatus: false,
      });
      this.stopResponse();
    }
  };

  sendMessage(action) {
    chrome.runtime.sendMessage({ action });
  }

  openDialog = () => {
    this.setState({
      dialog: true,
    });
  };

  textareaChange = (e) => {
    this.setState({
      textareaText: e.target.value,
    });
  };

  ckickOnPostBtn = () => {
    const { textareaText } = this.state;
    const lang = this.props.settings.language.filter(
      (el) => el.selected === true
    );

    const searchValueWithLanguageOption =
      lang[0].value === "auto"
        ? textareaText
        : `${textareaText}(in ${lang[0].value})`;

    this.setState(
      {
        nextQuestion: true,
        dialogLoader: true,
      },
      () => {
        this.postQuestion(searchValueWithLanguageOption, (event) => {
          this.createAnswer(event);
        });
      }
    );
  };

  stopResponse = (c) => {
    myPort.disconnect();

    this.setState({
      stopRespBtnStatus: false,
      showFooter: true,
      showCopyBox: true,
      showFeedbackBtn: true,
    });

    if (this.props.settings.removeDi === true) {
      chrome.runtime.sendMessage({ action: "REMOVE_DIALOGS" });
    }

    chrome.runtime.sendMessage(
      {
        action: "GEN_TITLE",
        id: this.state.answer.conversationId,
        mId: this.state.answer.data.messageId,
      },
      () => {
        if (c) {
          c();
        }
      }
    );
  };

  ckickOnFeedback = (e) => {
    const { answer } = this.state;
    const atr = e.target.attributes["data-action"].value;
    let data;
    let color;

    if (atr === "thumbsUp") {
      color = "#00c600";
      data = {
        conversation_id: answer.conversationId,
        message_id: answer.data.messageId,
        rating: "thumbsUp",
      };
    } else if (atr === "thumbsDown") {
      color = "#d73a49";
      data = {
        conversation_id: answer.conversationId,
        message_id: answer.data.messageId,
        rating: "thumbsDown",
      };
    }

    chrome.runtime.sendMessage({
      action: "FEEDBACK",
      data,
    });

    this.setState(
      {
        feedbackTextColor: color,
        showFeedbackText: true,
        showFeedbackBtn: false,
      },
      () => {
        setTimeout(() => {
          this.setState({
            feedbackTextColor: "",
            showFeedbackText: false,
          });
        }, 2500);
      }
    );
  };

  copyAnswer = () => {
    this.setState(
      {
        copyStatus: true,
      },
      () => {
        navigator.clipboard.writeText(this.state.answer.data.text);
        setTimeout(() => {
          this.setState({
            copyStatus: false,
          });
        }, 2500);
      }
    );
  };

  render() {
    const {
      dialog,
      showFooter,
      loader,
      textareaText,
      answerStack,
      dialogLoader,
      stopRespBtnStatus,
      showFeedbackBtn,
      feedbackTextColor,
      showFeedbackText,
      showCopyBox,
      copyStatus,
      errorText,
    } = this.state;

    const { showSettings, showLoginForm, headerText, openSettings, browser } =
      this.props;

    return (
      <>
        {errorText !== null ? (
          <div className="appl1_sgpt_cnt_errortext">
            {errorText} <br></br>
            <p className="appl1_sgpt_cnt_errortext_p">
              Please disable other applications that work with GPT
            </p>
          </div>
        ) : (
          <>
            <div className="appl1_sgpt_cnt_head">
              <div className="appl1_sgpt_cnt_head_titlebox">
                <div
                  className={
                    showLoginForm === true
                      ? "appl1_sgpt_cnt_head_icon appl1_sgpt_cnt_head_icon_notlogin"
                      : "appl1_sgpt_cnt_head_icon"
                  }
                ></div>
                <div className="appl1_sgpt_cnt_head_title">{headerText}</div>
                {showLoginForm === true ? null : (
                  <div
                    className={
                      showSettings === false
                        ? "appl1_sgpt_cnt_head_setbtn"
                        : "appl1_sgpt_cnt_head_setbtn appl1_sgpt_cnt_head_setbtn_open"
                    }
                    onClick={openSettings}
                  ></div>
                )}

                {showLoginForm === true ? null : dialog === false ? (
                  <div className="appl1_sgpt_cnt_head_stop_feed_box">
                    <div
                      style={{
                        display:
                          stopRespBtnStatus === false || showSettings === true
                            ? "none"
                            : "flex",
                      }}
                      className="appl1_sgpt_cnt_head_stop_resp"
                      onClick={this.stopResponse}
                    >
                      <span className="appl1_sgpt_cnt_head_stop_resp_icon"></span>
                      <p className="appl1_sgpt_cnt_head_stop_resp_text">Stop</p>
                    </div>

                    <div
                      className="appl1_sgpt_cnt_head_copy_box"
                      style={{
                        display:
                          showCopyBox === false || showSettings === true
                            ? "none"
                            : "flex",
                      }}
                      onClick={this.copyAnswer}
                    >
                      <div
                        className="appl1_sgpt_cnt_head_stop_feed_box_copy"
                        style={{
                          display: copyStatus === false ? "block" : "none",
                        }}
                      ></div>
                      <div
                        className="appl1_sgpt_cnt_head_stop_feed_box_check"
                        style={{
                          display: copyStatus === true ? "block" : "none",
                        }}
                      ></div>
                    </div>
                  </div>
                ) : null}
              </div>
              {showLoginForm === true ? (
                <p className="appl1_sgpt_cnt_head_subtitle">
                  Please login on OpenAI and reload this tab.
                </p>
              ) : null}
            </div>
            <div
              className="appl1_sgpt_cnt_main"
              style={{
                display: showSettings === false ? "block" : "none",
              }}
            >
              {this.props.showLoginForm === true ? (
                <>
                  <div
                    className="appl1_sgpt_cnt_loginbtn"
                    onClick={() => {
                      this.sendMessage("OPEN_LOGIN_PAGE");
                    }}
                  >
                    <div className="appl1_sgpt_cnt_loginbtn_icon"></div>
                    <p className="appl1_sgpt_cnt_loginbtn_text">
                      Login on OpenAI
                    </p>
                  </div>
                  <p className="appl1_sgpt_cnt_logintext">
                    OpenAI requires passing a security check every once in a
                    while.
                    <br></br>If this keeps happening, chnge AI provider to
                    OpenAI API in the{" "}
                    <span className="appl1_sgpt_cnt_logintext_span">
                      extension options
                    </span>
                  </p>
                </>
              ) : (
                <>
                  {loader === true ? (
                    <p className="appl1_sgpt_cnt_main_moment">One moment...</p>
                  ) : (
                    <>
                      {dialog === false ? (
                        <div className="appl1_sgpt_cnt_answer">
                          <Markdown answer={answerStack[0].a} />
                        </div>
                      ) : (
                        <>
                          <SGPTDialog
                            answerStack={answerStack}
                            stopRespBtnStatus={stopRespBtnStatus}
                            stopResponse={this.stopResponse}
                          />
                          {dialogLoader === false ? null : <DialogLoader />}
                        </>
                      )}
                      {showFooter === true ? (
                        <>
                          <SGPTMainFooter
                            dialog={dialog}
                            openDialog={this.openDialog}
                            textareaText={textareaText}
                            textareaChange={this.textareaChange}
                            ckickOnPostBtn={this.ckickOnPostBtn}
                            showFeedbackText={showFeedbackText}
                            feedbackTextColor={feedbackTextColor}
                            showFeedbackBtn={showFeedbackBtn}
                            showSettings={showSettings}
                            ckickOnFeedback={this.ckickOnFeedback}
                          />
                          <SGPTMainFooterText
                            getExtVersion={this.props.getExtVersion}
                            sendMessage={this.sendMessage}
                            browser={browser}
                          />
                        </>
                      ) : null}
                    </>
                  )}
                </>
              )}
            </div>
          </>
        )}
      </>
    );
  }
}
