import React from "react";

export default function SGPTMainFooterText({
  getExtVersion,
  sendMessage,
  browser,
}) {
  console.log(browser);
  return (
    <div className="appl1_sgpt_cnt_main_footer_text_box">
      <p className="appl1_sgpt_cnt_main_footer_text">
        {`ChatGPT for ${
          browser === "CHROME" ? "Chrome" : "Edge"
        } v ${getExtVersion()}`}
      </p>
      <p
        className="appl1_sgpt_cnt_main_footer_text_rateus"
        onClick={() => {
          sendMessage("OPEN_RATEUS_PAGE");
        }}
      >
        Rate us
      </p>
      <p
        className="appl1_sgpt_cnt_main_footer_text_support"
        onClick={() => {
          sendMessage("OPEN_SUPPORT_PAGE");
        }}
      >
        Get support
      </p>
    </div>
  );
}
