import React from "react";
import ReactDOM from "react-dom";
import "./content.scss";

import { getPossibleElementByQuerySelector } from "../services/utils";
import { containersList } from "../services/containersList";

//components
import SGPTContainer from "./components/SGPTContainer";
import QuestionBox from "./QuestionBox";
//

let flag = true;

function removeElement(config) {
  const { classes } = config;

  setInterval(() => {
    classes.forEach((item) => {
      if (item[0] === "className") {
        const el = document.querySelector("." + item[1]);
        if (el) {
          el.setAttribute("style", "display:none !important");
        }
      } else if (item[0] === "id") {
        const el = document.getElementById(item[1]);
        if (el) {
          el.setAttribute("style", "display:none !important");
        }
      }
    });
  }, 1000);
}

async function mount(config, question, siteConfig, siteName) {
  if (question && siteConfig) {
    const container = document.createElement("div");
    container.className = "appl1_sgpt_wrapper";

    if (siteName === "google") {
      let timer = setInterval(check, 100);

      function check() {
        if (flag === true) {
          const a = document.getElementById("rcnt");

          if (a) {
            const b = a.querySelector("#rhs");

            if (b) {
              if (b.querySelector(".appl1_sgpt_wrapper") === null) {
                b.prepend(container);
                flag = false;
              }
            } else {
              if (a.querySelector(".appl1_sgpt_wrapper") === null) {
                container.classList.add("appl1_sgpt_cnt_sidebar_single");
                a.appendChild(container);
                flag = false;
              }
            }
          }
        } else {
          clearInterval(timer);
        }
      }
    } else {
      const siderbarContainer = getPossibleElementByQuerySelector(
        siteConfig.sidebarContainerQuery
      );

      if (siderbarContainer) {
        siderbarContainer.prepend(container);
      } else {
        container.classList.add("appl1_sgpt_cnt_sidebar_single");
        const appendContainer = getPossibleElementByQuerySelector(
          siteConfig.appendContainerQuery
        );

        if (appendContainer) {
          appendContainer.appendChild(container);
        }
      }
    }

    ReactDOM.render(
      <SGPTContainer
        question={question}
        config={config}
        boxMode={"aForm"}
        imHelper={false}
      />,
      container
    );
  }
}

async function run(config) {
  const siteRegex = new RegExp(Object.keys(containersList).join("|"));
  const siteName = window.location.hostname.match(siteRegex);

  if (siteName) {
    const containersListConfig = containersList[siteName[0]];
    const searchInput = getPossibleElementByQuerySelector(
      containersListConfig.inputQuery
    );

    if (searchInput && searchInput.value) {
      const lang = config.language.filter((el) => el.selected === true);

      const searchValueWithLanguageOption =
        lang[0].value === "auto"
          ? searchInput.value
          : `${searchInput.value}(in ${lang[0].value})`;

      mount(
        config,
        searchValueWithLanguageOption,
        containersListConfig,
        siteName[0]
      );
    }
  }
}

function renderHelper(l, cfg) {
  const box = document.createElement("div");
  box.className = "appl1_sgpt_float_box";
  document.body.appendChild(box);

  ReactDOM.render(<QuestionBox lang={l} config={cfg} imHelper={true} />, box);
}

let config;
let lang;

chrome.storage.local.get(["appl1_sgpt_settings"], (result) => {
  config = result.appl1_sgpt_settings;
  lang = config.language.filter((el) => el.selected === true);

  if (config.siteHelper === true) {
    renderHelper(lang, config);
  }

  removeElement(config);
  run(config);
});

chrome.runtime.onMessage.addListener((message, sender, callback) => {
  if (message.action === "RELOAD_APP") {
    const el = document.querySelector(".appl1_sgpt_wrapper");
    if (el) {
      el.remove();
    }

    const el2 = document.querySelector(".appl1_sgpt_float_box");
    if (el2) {
      el2.remove();
    }

    flag = true;
    removeElement(config);
    run(config);
    renderHelper(lang, config);
  }
});
