import React, { Component, useState } from "react";
import SGPTContainer from "./components/SGPTContainer";

const QuestionboxInput = ({ clickOnPostBtn, boxMode }) => {
  const [inputValue, setInputValue] = useState("");

  return (
    <div
      className="appl1_sgpt_wrapper_questionbox_input"
      style={{
        display: boxMode === "qForm" ? "block" : "none",
      }}
    >
      <div className="appl1_sgpt_wrapper_questionbox_input_cont">
        <textarea
          placeholder="Ask me anything..."
          maxLength={1000}
          className="appl1_sgpt_wrapper_questionbox_input_textarea"
          value={inputValue}
          onChange={(e) => setInputValue(e.target.value)}
          onKeyDown={(e) => {
            if (e.code.toLocaleLowerCase() === "enter") {
              clickOnPostBtn(inputValue);
            }
          }}
        />
        <div className="appl1_sgpt_wrapper_questionbox_input_counter">
          <p className="appl1_sgpt_wrapper_questionbox_input_counter_first">
            {inputValue.length}
          </p>
          /
          <p className="appl1_sgpt_wrapper_questionbox_input_counter_second">
            1000
          </p>
        </div>
        <div
          className="appl1_sgpt_wrapper_questionbox_input_post"
          onClick={() => {
            clickOnPostBtn(inputValue);
          }}
        ></div>
      </div>
    </div>
  );
};

export default class Question extends Component {
  state = {
    question: "",
  };

  sendMessage(action) {
    chrome.runtime.sendMessage({ action });
  }

  clickOnPostBtn = (text) => {
    const { lang } = this.props;

    const searchValueWithLanguageOption =
      lang[0].value === "auto" ? text : `${text}(in ${lang[0].value})`;

    this.setState(
      {
        question: searchValueWithLanguageOption,
      },
      () => this.props.switchBoxMode("aForm")
    );
  };

  render() {
    const { question } = this.state;
    const { boxMode, config, authStatus } = this.props;

    return (
      <>
        {authStatus !== null ? (
          authStatus === false ? (
            <div className="appl1_sgpt_wrapper_questionbox_login">
              <p className="appl1_sgpt_wrapper_questionbox_login_text">
                Please login on OpenAI.
              </p>
              <div
                className="appl1_sgpt_wrapper_questionbox_login_btn"
                onClick={() => {
                  this.sendMessage("OPEN_LOGIN_PAGE");
                }}
              >
                <div className="appl1_sgpt_wrapper_questionbox_btn_icon"></div>
                <p className="appl1_sgpt_wrapper_questionbox_btn_text">
                  Login on OpenAI
                </p>
              </div>
            </div>
          ) : (
            <>
              <QuestionboxInput
                boxMode={boxMode}
                clickOnPostBtn={this.clickOnPostBtn}
              />

              <SGPTContainer
                boxMode={boxMode}
                question={question}
                config={config}
              />
            </>
          )
        ) : null}
      </>
    );
  }
}
