import React from "react";

export default function SGPTMainFooter({
  dialog,
  openDialog,
  textareaText,
  textareaChange,
  ckickOnPostBtn,
  showFeedbackBtn,
  showSettings,
  ckickOnFeedback,
  feedbackTextColor,
  showFeedbackText,
}) {
  return (
    <div className="appl1_sgpt_cnt_main_footer">
      {dialog === false ? (
        <>
          <div className="appl1_sgpt_cnt_main_footer_btn" onClick={openDialog}>
            <div className="appl1_sgpt_cnt_main_footer_btn_icon"></div>
            <div className="appl1_sgpt_cnt_main_footer_btn_text">
              Continue dialog
            </div>
          </div>
          <div
            className="appl1_sgpt_cnt_head_feedback"
            style={{
              display:
                showFeedbackBtn === false || showSettings === true
                  ? "none"
                  : "flex",
            }}
          >
            <div
              className="appl1_sgpt_cnt_head_feedback_up"
              data-action="thumbsUp"
              onClick={ckickOnFeedback}
            ></div>
            <div
              className="appl1_sgpt_cnt_head_feedback_down"
              data-action="thumbsDown"
              onClick={ckickOnFeedback}
            ></div>
          </div>
          <div
            className="appl1_sgpt_cnt_head_feedback_text"
            style={{
              display: showFeedbackText === false ? "none" : "block",
              color: feedbackTextColor,
            }}
          >
            Feedback<br></br>sended
          </div>
        </>
      ) : (
        <div className="appl1_sgpt_cnt_main_footer_inputbox">
          <textarea
            placeholder="Ask me anything..."
            maxLength={1000}
            className="appl1_sgpt_cnt_main_footer_inputbox_textarea"
            value={textareaText}
            onChange={textareaChange}
            onKeyDown={(e) => {
              if (e.code.toLocaleLowerCase() === "enter") {
                ckickOnPostBtn();
              }
            }}
          />
          <div className="appl1_sgpt_cnt_main_footer_inputbox_counter">
            <p className="appl1_sgpt_cnt_main_footer_inputbox_counter_first">
              {textareaText.length}
            </p>
            /
            <p className="appl1_sgpt_cnt_main_footer_inputbox_counter_second">
              1000
            </p>
          </div>
          <div
            className="appl1_sgpt_cnt_main_footer_inputbox_post"
            onClick={ckickOnPostBtn}
          ></div>
        </div>
      )}
    </div>
  );
}
