import React, { Component } from "react";
import ReactMarkdown from "react-markdown";
import rehypeHighlight from "rehype-highlight";

class CodeCard extends Component {
  state = {
    copied: false,
  };

  copyCode = (e) => {
    if (e.currentTarget) {
      const text = e.currentTarget.parentElement.nextSibling.innerText;

      this.setState(
        {
          copied: true,
        },
        () => {
          navigator.clipboard.writeText(text);

          setTimeout(() => {
            this.setState({
              copied: false,
            });
          }, 3000);
        }
      );
    }
  };

  render() {
    const { lang, clasName, children, props } = this.props;
    const { copied } = this.state;

    return (
      <div>
        <div className="appl1_sgpt_cnt_markdown_code_header">
          {lang}
          <div
            className="appl1_sgpt_cnt_markdown_code_header_copy"
            onClick={this.copyCode}
          >
            <p
              className="appl1_sgpt_cnt_markdown_code_header_copy_completed"
              style={{
                opacity: copied === false ? "0" : "1",
              }}
            >
              Copied!
            </p>
            {copied === false ? (
              <>
                <div className="appl1_sgpt_cnt_markdown_code_header_copy_icon"></div>
                <div className="appl1_sgpt_cnt_markdown_code_header_copy_p">
                  Copy
                </div>
              </>
            ) : null}
          </div>
        </div>
        <code className={clasName} {...props}>
          {children}
        </code>
      </div>
    );
  }
}

export default function Markdown({ answer }) {
  return (
    <ReactMarkdown
      rehypePlugins={[[rehypeHighlight, { detect: true }]]}
      className="appl1_sgpt_cnt_markdown"
      children={answer}
      components={{
        code({ node, inline, className, children, ...props }) {
          const match = /language-(\w+)/.exec(className || "");
          let lang;
          if (className) {
            lang = className.split("-")[1];
          }

          return !inline && match ? (
            <CodeCard
              lang={lang}
              clasName={className}
              children={children}
              props={props}
            />
          ) : (
            <code className={className} {...props}>
              {children}
            </code>
          );
        },
      }}
    />
  );
}
