import React, { Component } from "react";
import Question from "./Question";
import { detectSystemColorScheme } from "../services/utils";

export default class QuestionBox extends Component {
  state = {
    authStatus: false,
    showBox: false,
    boxMode: "qForm",
    errorText: null,
  };

  componentDidMount() {
    this.getToken();
  }

  getToken = () => {
    const _this = this;

    chrome.runtime.sendMessage({ action: "GET_ACCESS_TOKEN" }, (data) => {
      if (data.authStatus === false) {
        _this.setState({
          authStatus: false,
          boxMode: "qForm",
        });
      } else {
        _this.setState({
          authStatus: true,
        });
      }

      if (data.details === "Rate limit exceeded") {
        _this.setState({
          errorText: data.details,
        });
      }
    });
  };

  clickOpenBox = () => {
    const { showBox } = this.state;

    if (showBox === false) {
      this.setState(
        {
          showBox: true,
        },
        () => this.getToken()
      );
    } else if (showBox === true) {
      this.setState({
        showBox: false,
        boxMode: "qForm",
      });
    }
  };

  clickOnBack = () => {
    this.setState({
      boxMode: "qForm",
    });
  };

  getColorTheme = () => {
    if (this.props.config !== null) {
      let userTheme;
      const { theme } = this.props.config;

      if (theme === "auto") {
        userTheme = detectSystemColorScheme();
      } else {
        userTheme = theme;
      }

      return userTheme;
    }
  };

  switchBoxMode = (boxMode) => {
    this.setState({
      boxMode,
    });
  };

  clickOnHideHelper = () => {
    document.querySelector(".appl1_sgpt_float_box").remove();
  };

  render() {
    const { showBox, boxMode, authStatus, errorText } = this.state;

    return (
      <>
        <div
          className={
            this.getColorTheme() === "dark"
              ? "appl1_sgpt_wrapper_questionbox appl1_sgpt_wrapper_questionbox_dark"
              : "appl1_sgpt_wrapper_questionbox"
          }
        >
          <div
            style={{
              display: showBox === false ? "none" : "block",
            }}
          >
            {errorText !== null ? (
              <p className="appl1_sgpt_wrapper_questionbox_error">
                Error: {errorText}
              </p>
            ) : null}

            {errorText === null ? (
              <Question
                config={this.props.config}
                lang={this.props.lang}
                boxMode={boxMode}
                authStatus={authStatus}
                switchBoxMode={this.switchBoxMode}
              />
            ) : null}
          </div>

          <div className="appl1_sgpt_float_box_buttons">
            <div
              className={
                showBox === false
                  ? "appl1_sgpt_float_box_btn"
                  : "appl1_sgpt_float_box_btn appl1_sgpt_float_box_btn_opened"
              }
              onClick={this.clickOpenBox}
            ></div>
            <div
              className="appl1_sgpt_float_box_btn_back"
              onClick={this.clickOnBack}
              style={{
                display: boxMode === "aForm" ? "flex" : "none",
              }}
            ></div>
          </div>
          <div
            style={{
              display: showBox === true ? "none" : "block",
            }}
            className="appl1_sgpt_float_btn_hide_box"
            onClick={this.clickOnHideHelper}
          ></div>
        </div>
      </>
    );
  }
}
