import React from "react";
import "./Settings.scss";

import TriggerMode from "./TriggerMode";
import Theme from "./Theme";
import Language from "./Language";
import Helper from "./Helper";
import Dailogs from "./Dailogs";
import Provider from "./Provider";

export default function Settings({
  settings,
  setToSettings,
  extVersion,
  browser,
}) {
  function clickOnRateUs() {
    chrome.runtime.sendMessage({ action: "OPEN_RATEUS_PAGE" });
  }

  function clickOnSupport(params) {
    chrome.runtime.sendMessage({ action: "OPEN_SUPPORT_PAGE" });
  }

  return (
    <div className="appl1_sgpt_app_settings_wrapper">
      <div className="appl1_sgpt_app_settings_container">
        <TriggerMode
          setToSettings={setToSettings}
          triggerMode={settings.triggerMode}
        />
        <Theme setToSettings={setToSettings} theme={settings.theme} />
        <Language setToSettings={setToSettings} language={settings.language} />
        <div
          className="appl1_sgpt_section_header"
          style={{
            marginBottom: "20px",
          }}
        >
          Additional settings
        </div>
        <Helper setToSettings={setToSettings} status={settings.siteHelper} />
        <Dailogs setToSettings={setToSettings} status={settings.removeDi} />
        <Provider setToSettings={setToSettings} provider={settings.provider} />
        <div className="appl1_sgpt_app_settings_footer">
          <p className="appl1_sgpt_app_settings_footer_text">
            ChatGPT for {browser === "CHROME" ? "Chrome" : "Edge"} v{" "}
            {extVersion()}
          </p>
          <p className="appl1_sgpt_app_settings_rateus" onClick={clickOnRateUs}>
            Rate us
          </p>
          <p
            className="appl1_sgpt_app_settings_support"
            onClick={clickOnSupport}
          >
            Get support
          </p>
        </div>
      </div>
    </div>
  );
}
