import { createParser } from "eventsource-parser";
import ExpiryMap from "expiry-map";

function getBrowser() {
  let brow = "CHROME";

  navigator.userAgentData.brands.forEach((element) => {
    const b = element.brand.toLowerCase();

    if (b.includes("edge")) {
      brow = "EDGE";
    }
  });

  return brow;
}

async function* streamAsyncIterable(stream) {
  const reader = stream.getReader();
  try {
    while (true) {
      const { done, value } = await reader.read();
      if (done) {
        return;
      }
      yield value;
    }
  } finally {
    reader.releaseLock();
  }
}

function getPossibleElementByQuerySelector(queryArray) {
  for (const query of queryArray) {
    const element = document.querySelector(query);

    if (element) {
      return element;
    }
  }
}

async function parsData(resp, cb) {
  const parser = createParser((event) => {
    if (event.type === "event") {
      cb(event.data);
    }
  });

  for await (const chunk of streamAsyncIterable(resp.body)) {
    const str = new TextDecoder().decode(chunk);
    parser.feed(str);
  }
}

async function getChatGPTAccessToken(cb) {
  const KEY_ACCESS_TOKEN = "accessToken";
  const cache = new ExpiryMap(10 * 1000);
  if (cache.get(KEY_ACCESS_TOKEN)) {
    cb(cache.get(KEY_ACCESS_TOKEN));
  }
  const resp = await fetch("https://chat.openai.com/api/auth/session");
  if (resp.status === 403) {
    const a = {
      authStatus: false,
    };
    cb(a);
  }
  const data = await resp.json().catch(() => ({}));
  if (!data.accessToken) {
    data.authStatus = false;
    cb(data);
  }
  cache.set(KEY_ACCESS_TOKEN, data.accessToken);
  cb(data);
}

function detectSystemColorScheme() {
  if (
    window.matchMedia &&
    window.matchMedia("(prefers-color-scheme: dark)").matches
  ) {
    return "dark";
  }
  return "light";
}

function getGuideMode(guide, question) {
  if (guide === "default") {
    return `You are a large language model trained by OpenAI. Your job is to generate human-like text based on the input you received, allowing it to engage in natural-sounding conversations and provide responses that are coherent and relevant to the topic at hand.

If the input is a question, try your best to answer it. Otherwise, provide as much information as you can.
You should use "code blocks" syntax from markdown including programing language name to encapsulate any part in responses that's longer-format content such as poem, code, lyrics.
Provide programming language name in code blocks if possible.
You should also use bold syntax from markdown on the relevant parts of the responses to improve readability.
If your answer contains code, make sure to provide detailed explanations.
You can understand and communicate fluently in the user's language of choice such as English,,,Espanol,Francais or Deutsch.

Here is a conversation between human A and you:

A: ${question}
You:`;
  } else if (guide === "simple") {
    return `You are a large language model trained by OpenAI that generate human-like text based on the input you received.
Here is a conversation between human A and you:

A: ${question}
You:`;
  }
}

function endsWithQuestionMark(question) {
  return (
    question.endsWith("?") || // ASCII
    question.endsWith("") || // Chinese/Japanese
    question.endsWith("") || // Arabic
    question.endsWith("") // Arabic
  );
}

export {
  getPossibleElementByQuerySelector,
  parsData,
  getChatGPTAccessToken,
  detectSystemColorScheme,
  getGuideMode,
  endsWithQuestionMark,
  getBrowser,
};
