import React from "react";

function TriggerMode({ setToSettings, triggerMode }) {
  const change = (e) => {
    setToSettings("triggerMode", e.target.value);
  };

  return (
    <div className="appl1_sgpt_app_settings_trigger_section">
      <div
        className="appl1_sgpt_section_header"
        style={{
          marginBottom: "24px",
        }}
      >
        Chatgpt Trigger mode
      </div>
      <div className="appl1_sgpt_settings_trigger_container" onChange={change}>
        <label className="appl1_sgpt_trigger_container_item">
          <input
            type="radio"
            value="always"
            name="trigger"
            className="appl1_sgpt_trigger_container_item_input"
          />
          <div className="appl1_sgpt_trigger_container_item_checkbox">
            <div
              className={
                triggerMode === "always"
                  ? "appl1_sgpt_trigger_container_item_circle active"
                  : "appl1_sgpt_trigger_container_item_circle"
              }
            >
              <div
                className={
                  triggerMode === "always"
                    ? "appl1_sgpt_trigger_container_item_circle_inner active"
                    : "appl1_sgpt_trigger_container_item_circle_inner"
                }
              ></div>
            </div>
            <div className="appl1_sgpt_trigger_container_item_checkbox_p">
              Always
            </div>
          </div>
        </label>

        <label className="appl1_sgpt_trigger_container_item">
          <input
            type="radio"
            value="question"
            name="trigger"
            className="appl1_sgpt_trigger_container_item_input"
          />
          <div className="appl1_sgpt_trigger_container_item_checkbox">
            <div
              className={
                triggerMode === "question"
                  ? "appl1_sgpt_trigger_container_item_circle active"
                  : "appl1_sgpt_trigger_container_item_circle"
              }
            >
              <div
                className={
                  triggerMode === "question"
                    ? "appl1_sgpt_trigger_container_item_circle_inner active"
                    : "appl1_sgpt_trigger_container_item_circle_inner"
                }
              ></div>
            </div>
            <div className="appl1_sgpt_trigger_container_item_checkbox_p">
              Question Mark
            </div>
          </div>
        </label>

        <label className="appl1_sgpt_trigger_container_item">
          <input
            type="radio"
            value="manually"
            name="trigger"
            className="appl1_sgpt_trigger_container_item_input"
          />
          <div className="appl1_sgpt_trigger_container_item_checkbox">
            <div
              className={
                triggerMode === "manually"
                  ? "appl1_sgpt_trigger_container_item_circle active"
                  : "appl1_sgpt_trigger_container_item_circle"
              }
            >
              <div
                className={
                  triggerMode === "manually"
                    ? "appl1_sgpt_trigger_container_item_circle_inner active"
                    : "appl1_sgpt_trigger_container_item_circle_inner"
                }
              ></div>
            </div>
            <div className="appl1_sgpt_trigger_container_item_checkbox_p">
              Manually
            </div>
          </div>
        </label>
      </div>
    </div>
  );
}

export default TriggerMode;
